.contactItem {
    margin-bottom: 4%;
    display: flex;
}

.contactItem__left {
    padding: 0 15px;
}

.contactItem__left > .MuiSvgIcon-root {
    font-size: var(--icon);
    color: var(--custom);
}

.contactItem__right > h4 {
    color: var(--white);
    padding-bottom: 1%;
    font-size: 17px;
    letter-spacing: 1px;
}

.contactItem__right > p {
    color: var(--white);
    font-weight: lighter;  
}

@media screen and (max-width: 1124px) {
    .contactItem__right > p {
        font-size: smaller; 
    }
}

@media screen and (max-width: 414px) {
    .contactItem__left > .MuiSvgIcon-root {
        font-size: 25px;
    }
    
    .contactItem__right > h4 {
        font-size: 15px;
    }
    
    .contactItem__right > p {
        font-size: 12px; 
    }
}

@media screen and (max-width: 360px) {
    .contactItem__right > h4 {
        letter-spacing: 0;
        font-size: 14px;
    }

    .contactItem__right > p {
        font-size: 12px; 
    }
}