.nav {
    display: flex;
    justify-content: space-between;
    padding: 2% 0;
    position: sticky;
    top: 0;
    transition: 0.5s;
    z-index: 9;
    border-bottom: 2px solid #3e3e3e;
}

.nav__left {
    display: flex;
    align-items: center;
    color: var(--white);
}

.nav__left > .span {
    text-decoration: none;
}

.nav__left > .span > span {
    padding: 5px 15px;
    margin-right: 10px;
    font-size: 25px;
    font-weight: bold;
    background-color: var(--custom);
    color: var(--white);
    border-radius: 50%;
    box-shadow: -1px 1px 2px 1px rgba(0,0,0,.3);
    cursor: pointer;
}

.nav__left > h3 {
    font-weight: 400;
}

.nav__right {
    color: var(--p);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex: 1;
}

.nav__right > ul {
    display: flex;
    justify-content: space-between;
}

.nav__right > ul > .li {
    text-decoration: none;
    color: var(--p);
}

.nav__right > ul > .li > li {
    display: flex;
    cursor: pointer;
    transition: 0.3s ease-in;
    margin-left: 30px;
}

.nav__right > ul > .li:hover {
    color: var(--white);
}

.nav__icon {
    display: flex;
    align-items: center;
    padding: 10px;
    color: var(--custom);
    cursor: pointer;
    display: none;
}

.nav__icon > .MuiSvgIcon-root {
    font-size: 30px;
}

@media screen and (max-width: 1024px) {
    .nav__left > h3 {
        font-weight: 400;
        font-size: 15px;
    }
}

@media screen and (max-width: 960px) {

.nav__icon {
    display: inline;
}

.nav__menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -130%;
    opacity: 1;

    }

    .nav__menu.active {
    background: #242124;
    top: 80px;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    } 

    .nav__menu.active > ul {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .nav__menu.active > ul > .li {
        padding: 5% 2%;
        margin-top: 5%;
        width: 100%;
        border: 1px solid var(--p);
        border-radius: 50px;
        transition: 0.3 ease;
    }

    .nav__menu.active > ul > .li:hover {
        border: 1px solid var(--custom);
    }

    .nav__menu.active > ul > .li > li > .MuiSvgIcon-root {
        display: none;
    }
}

@media screen and (max-width: 700px) {    
.nav__left > span {
    font-size: 18px;
}

.nav__left > h3 {
    font-size: 13px;
}
}