.portfolio__content {
    margin-top: 2%;
    margin-bottom: 4%;
    display: flex;
    flex-direction: column;
}

.portfolio__nav > ul {
    display: flex;
    justify-content: space-between;
    width: 20%;
    margin: 0 auto 2% auto;
}

.portfolio__nav > ul > li {
    color: var(--p);
    font-size: 15px;
    transition: 0.3s ease-in;
}

.portfolio__nav > ul > li:hover {
    color: var(--white);
    cursor: pointer;
}

.portfolio__main {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
    width: calc(100% + 20px);  
    position: relative;
}

@media screen and (max-width: 1200px) {
    .portfolio__nav > ul {
        width: 35%;
    }
}

@media screen and (max-width: 1024px) {
    .portfolio__nav {
        margin-bottom: 2%;
        margin-top: 2%;
    }

    .portfolio__nav > ul {
        width: 30%;
    }
}

@media screen and (max-width: 800px) {
    .portfolio__nav > ul {
        width: 40%;
    }
}

@media screen and (max-width: 650px) {
    .portfolio__nav {
        margin-bottom: 3%;
        margin-top: 3%;
    }

    .portfolio__nav > ul {
        width: 40%;
    }
}

@media screen and (max-width: 600px) {
    .portfolio__nav > ul {
        width: 50%;
    }
}

@media screen and (max-width: 360px) {
    .portfolio__nav {
        margin-bottom: 4%;
        margin-top: 4%;
    }

    .portfolio__nav > ul {
        width: 70%;
    }

    .portfolio__nav > ul > li {
        font-size: 13px;
    }
}