.contact {
    margin-bottom: 4%;
    position: relative;
}

.contact__content {
    margin-top: 2%;
    display: flex;
}

.contact__contentLeft {
    flex: 0.4;
}

.contact__contentRight {
    flex: 0.6;
    margin-left: 4%;
}

.contact__contentRight > h3 {
    color: var(--white);
    width: 200px;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #3e3e3e;
    margin-bottom: 2%;
}

.contact__contentRight >h3:after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 30px;
    border-bottom: 2px solid var(--custom);
}

.contact__contentRight > form {
    display: flex;
    justify-content: space-between;
}

.form__left {
    flex: 0.48;
}

.form__input {
    width: 100%;
    border: 2px solid var(--p);
    margin-bottom: 4%;
    padding: 1.5% 2%;
    outline: none;
    color: var(--p);
    transition: 0.4s;
}

.form__input::placeholder {
    color: var(--p);
}

.form__right {
    flex: 0.48;
}

.form__right > textarea {
    width: 100%;
    height: 70%;
    border: 2px solid var(--p);
    padding: 1.5% 2%;
    outline: none;
    color: var(--p);
    transition: 0.4s;
}

.form__input:focus,
.form__right > textarea:focus {
    border: 2px solid var(--custom);
}

@media screen and (max-width: 1124px) {
    .contact__content {
        margin-top: 4%;
    }

    .contact__contentRight > h3 {
        margin-bottom: 4%;
    }

    .contact__contentRight > form {
        display: flex;
        flex-direction: column;
    }

    .form__left {
        flex: 1;
    }

    .form__right {
        flex: 1;
    }

    .form__right > textarea {
        height: 100%;
        margin-bottom: 3%;
    }
}

@media screen and (max-width: 800px) {
    .contact__content {
        margin-top: 2%;
        display: flex;
        flex-direction: column;
    }
    
    .contact__contentLeft {
        flex: 1;
        margin-top: 6%;
    }

    .contact__contentRight {
        flex: 1;
        margin-left: 0;
        margin-top: 6%;
    }
}