.work {
    display: flex;
    width: calc(100% / 2 - 50px);
    margin-top: 2%;
}

.work__icon {
    padding: 0 15px;
}

.work__icon > .MuiSvgIcon-root {
    font-size: var(--icon);
    color: var(--custom);
}

.work__content > h4 {
    color: var(--white);
    padding-bottom: 1%;
    font-size: 17px;
    letter-spacing: 1px;
}

.work__content > p {
    color: var(--p);
}

@media screen and (max-width: 1000px) {
    .work {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 100%;
        margin-top: 4%;
    }

    .work__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .work__content > p {
        width: 80%;
    }
}

@media screen and (max-width: 700px) {
    .work {
        margin-bottom: 2%;
    }
}

@media screen and (max-width: 414px) {
    .work__content > p {
        font-size: 12px;
    }
}