.heading {
    display: flex;
    border-bottom: 2px solid #3e3e3e;
}

.heading__left {
    padding: 3% 0;
    flex: 1;
    color: var(--white);
    font-size: 40px;
    font-weight: bolder;
}

.heading__right {
    color: var(--p);
    margin-top: 1%;
    margin-right: 3%;
}

@media screen and (max-width: 1024px) {
    .heading__left {
        font-size: 35px;
    }

    .heading__right {
        font-size: smaller;
    }
}

@media screen and (max-width: 800px) {
    .heading__left {
        font-size: 30px;
    }

    .heading__right {
        font-size: small;
    }
}

@media screen and (max-width: 414px) {
    .heading__left {
        font-size: 20px;
    }
}