.intro {
    display: flex;
    margin: 5% auto 0 auto;
    width: 80%;
}

.intro__left {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-left: 5%;
}

.intro__left__outer {
    width: 450px;
    height: 450px;
    background-color: #444345;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: -1px -1px 59px 9px rgba(0,0,0,0.75);
}

.intro__left__inner {
    width: 400px;
    height: 400px;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
}

.intro__left__inner > img {
    width: 400px;
    position: absolute;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.intro__left__inner > img:hover {
    transform: scale(1.1);
}

.intro__right {
    margin-left: 5%;
}

.intro__right > span {
    color: var(--p);
}

.intro__right > h1 {
    color: var(--white);
    margin-top: 7px;
    font-size: 45px;
}

.intro__right > p {
    color: #adadad;
    margin-top: 1.5%;
    width: 80%;
    line-height: 1.7;
}

.intro__right__btns {
    margin-top: 5%;
    display: flex;
}

.intro__link {
    text-decoration: none;
}

@media screen and (max-width: 1412px) { 
    .intro__left__outer {
        width: 380px;
        height: 380px;
    }
    
    .intro__left__inner {
        width: 330px;
        height: 330px;
    }
    
    .intro__left__inner > img {
        width: 330px;
    }

    .intro__right > h1 {
        font-size: 35px;
    }
    
    .intro__right > p {
        font-size: 14px;
    }
}

@media screen and (max-width: 1136px) {
    .intro {
        flex-direction: column;
    }

    .intro__left {
        margin-left: 0;
    }

    .intro__right {
        margin-left: 0;
        margin-top: 4%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}

@media screen and (max-width: 648px) {

    .intro__left {
        margin: 6% 0;
    }
    
    .intro__left__outer {
        width: 320px;
        height: 280px;
    }
    
    .intro__left__inner {
        width: 280px;
        height: 250px;
    }
    
    .intro__left__inner > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .intro__right > h1 {
        font-size: 30px;
    }
    
    .intro__right > p {
        font-size: 14px;
        width: 100%;
    }
}


@media screen and (max-width: 360px) {
    
    .intro__left__outer {
        width: 320px;
        height: 280px;
    }
    
    .intro__left__inner {
        width: 240px;
        height: 250px;
    }
    
    .intro__left__inner > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .intro__right > p {
        font-size: 12px; 
        width: 100%;
    }
}
