.factItem {
    width: calc(100% / 4 - 30px);
    height: 250px;
    background-color: #333333;
    border: 2px solid var(--p);
    border-radius: 15px;
    margin-bottom: 2%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.factItem__icon,  
.factItem__icon > .MuiSvgIcon-root,
.factItem > span {
    background-color: #333333;
}

.factItem__icon > .MuiSvgIcon-root {
    color: var(--custom);
    font-size: 60px;
    transition: 0.5s;
}

.factItem > span {
    color: var(--white);
}

.factItem > span {
    font-size: 20px;
    margin-top: 2%;
}

.factItem:hover > .MuiSvgIcon-root {
    transform: rotate(45deg);
}

@media screen and (max-width: 1000px) {
    .factItem {
        width: calc(100% / 3 - 30px);
        height: 230px;
        margin-top: 4%;
    }
}

@media screen and (max-width: 750px) {
    .factItem {
        width: calc(100% / 2 - 30px);
        height: 230px;
    }
}

@media screen and (max-width: 600px) {
    .factItem {
        width: 90%;
        height: 230px;
        margin: 0 auto;
        margin-top: 4%;
    }
}

@media screen and (max-width: 414px) {
    .factItem {
        height: 180px;
    }

    .factItem__icon > .MuiSvgIcon-root {
        font-size: 45px;
    }

    .factItem > span {
        font-size: 17px;
    }
}