:root {
  --custom: #0080ff;
  --p: #8e8d8f;
  --white: #f8f8f8;
  --icon: 35px;
}

* {
  background-color: #242124;
  font-family: 'Poppins', sans-serif;
  user-select: none;
}

ul, li, a {
  text-decoration: none;
  list-style: none;
}

.app {
  margin: 0 4%;
}

.active {
  font-weight: bolder;
  color: var(--white) !important;
}