.bar {
    margin: 2% 10% 0 4%;
}

.bar__top {
    display: flex;
    justify-content: space-between;
}

.bar__top > h5 {
    color: var(--white);
}

.bar__top > span {
    color: var(--p);
    font-size: smaller;
}

.bar__bottom {
    width: 100%;
    height: 10px;
    background-color: transparent;
    border: 1px solid var(--p);
    border-radius: 10px;
    margin-top: 1%;
}

@media screen and (max-width: 950px) {
    .bar {
        margin: 2% 4% 0 4%;
    }
}