/* ------------------ General ---------------------- */
.resume {
    margin-bottom: 4%;
}

.resume__content,
.skills__content {
    display: flex;
    margin-top: 2%;
}

.resume__content__left,
.resume__content__right,
.skills__content__left,
.skills_content__right {
    flex: 0.5;
}

.resume__content__left > h3,
.resume__content__right > h3 {
    color: var(--white);
    width: 100px;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #3e3e3e;
}

.resume__content__left >h3:after,
.resume__content__right >h3:after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 30px;
    border-bottom: 2px solid var(--custom);
}

.resume__content__leftDiv,
.resume__content__rightDiv {
    margin: 2% 4% 0;
    border-left: 2px solid #3e3e3e;
}

/* ----------------------- Skills --------------------------- */
.skills__content__left > h3,
.skills_content__right > h3 {
    color: var(--white);
    width: 150px;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #3e3e3e;
}

.skills__content__left >h3:after,
.skills_content__right >h3:after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 30px;
    border-bottom: 2px solid var(--custom);
}

@media screen and (max-width: 1124px) {
    .resume {
        margin-bottom: 6%;
    }

    .resume__content__left > h3 {
        margin-bottom: 4%;
    }

    .resume__content__right > h3 {
        margin-bottom: 4%;
    }

    .resume__content__left,
    .resume__content__right,
    .skills__content__left,
    .skills_content__right {
        flex: 1;
    }
}

@media screen and (max-width: 950px) {
    .resume {
        margin-bottom: 10%;
    }

    .resume__content,
    .skills__content {
        display: flex;
        flex-direction: column;
        margin-top: 2%;
    }

    .skills__content__left {
        margin-bottom: 4%;
    }

    .skills__content__left > h3,
    .skills_content__right > h3,
    .resume__content__left > h3,
    .resume__content__right > h3 {
        margin-top: 5%;
    }
}


