.fact {
    margin-bottom: 5%;
}

.fact > h3 {
    color: var(--white);
    width: 120px;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #3e3e3e;
}

.fact >h3:after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 30px;
    border-bottom: 2px solid var(--custom);
}

.fact__info {
    margin-top: 2%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media screen and (max-width: 1136px) {
    .fact {
        margin-top: 10%;
    }
}

@media screen and (max-width: 647px) {
    .fact {
        margin-top: 15%;
    }
}