.portfolioModal {
    width: 60%;
    height: 70%;
    box-shadow: 0px 0px 44px 16px rgba(0,0,0,0.75);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9;
}

.portfolioModal > iframe {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
    outline: none;
    padding: 1%;
    position: relative;
}

.portfolioModal > button,
.portfolioModal > button > .MuiSvgIcon-root {
    background-color: transparent;
}

.portfolioModal > button {
    position: absolute;
    top: 0;
    left: 50%;
    color: var(--custom);
    z-index: 99;
    border: none;
    outline: none;
    cursor: pointer;
}

.portfolioModal > button > .MuiSvgIcon-root {
    font-size: 35px;
    opacity: 0;
    transition: 0.3s;
    margin-top: 10px;
}

.portfolioModal:hover > button > .MuiSvgIcon-root {
    opacity: 1;
    margin-top: 20px;
}

@media screen and (max-width: 1680px) {
    .portfolioModal {
        width: 70%;
    }
}

@media screen and (max-width: 1200px) {
    .portfolioModal {
        width: 80%;
    }
}

@media screen and (max-width: 1024px) {
    .portfolioModal > button > .MuiSvgIcon-root {
        font-size: 30px;
        opacity: 1;
        transition: none;
        margin-top: 20px;
    }
}

@media screen and (max-width: 950px) {
    .portfolioModal {
        width: 90%;
    }
}

@media screen and (max-width: 600px) {
    .portfolioModal {
        width: 95%;
        max-height: 400px;
    }
}

@media screen and (max-width: 360px) {
    .portfolioModal {
        width: 95%;
        max-height: 300px;
    }
}