.portfolioItem {
    width: calc(100% / 3 - 20px);
    height: 350px;
    margin-left: 20px;
    margin-bottom: 20px;
    background-color: var(--p);
    border-radius: 10px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.portfolioItem > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(80%);
    transition: 0.5s ease-in-out;
}

.portfolioItem:hover > img {
    filter: brightness(100%);
    transform: scale(1.05);
}

.portfolioItem > span {
    position: absolute;
    top: 3%;
    left: 2%;
    z-index: 2;
    color: var(--white);
    font-size: smaller;
    background-color: #242124;
    padding: 2%;
    border-radius: 5px;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.portfolioItem:hover > span {
    opacity: 1;
}

@media screen and (max-width: 1124px) {
    .portfolioItem {
        width: calc(100% / 2 - 20px);
        height: 300px;
    }
}

@media screen and (max-width: 700px) {
    .portfolioItem {
        width: 100%;
        height: 300px;
    }
}