.footer {
    display: flex;
    justify-content: space-between;
    padding: 1% 0;
    border-top: 2px solid #3e3e3e;
}

.footer__left {
    color: var(--p);
}

.footer__left  > a {
    margin-right: 30px;
    font-size: 13px;
    cursor: pointer;
    color: var(--p);
}

.footer__right > span {
    color: var(--p);
    font-size: 13px;
    font-weight: bolder;
}

@media screen and (max-width: 850px) {
    .footer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .footer__left {
        margin-bottom: 2%;
    }
}

@media screen and (max-width: 650px) {
    .footer {
        text-align: center;
    }

    .footer__left  > a {
        margin-right: 10px;
        font-size: 10px;
    }

    .footer__right > span {
        font-size: 12px;
    }
}

