.about {
    margin-bottom: 5%;
}

.about > h3 {
    color: var(--white);
    width: 100px;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #3e3e3e;
}

.about >h3:after {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 30px;
    border-bottom: 2px solid var(--custom);
}

.about__info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

@media screen and (max-width: 1136px) {
    .about {
        margin-top: 10%;
    }
}

@media screen and (max-width: 647px) {
    .about {
        margin-top: 15%;
    }
}