.resumeItem {
    margin-bottom: 2%;
}

.resumeItem__top {
    display: flex;
    align-items: center;
}

.resumeItem__top > span {
    color: var(--white);
    border: 3px solid var(--custom);
    border-radius: 25px;
    padding: 2px 10px;
    margin-left: -20px;
}

.resumeItem__top > p {
    color: var(--p);
    margin-left: 2%;
    font-size: small;
}

.resumeItem__bottom {
    margin-left: 2%;
    margin-top: 2%;
    color: var(--white);
    padding-bottom: 2%;
    border-bottom: 2px solid #2e2c2e;
}

.resumeItem__bottom > h4 {
    margin-bottom: 1%;
}

.resumeItem__bottom > p {
    font-weight: lighter;
    font-size: smaller;
    line-height: 1.7;
}

@media screen and (max-width: 1124px) {
    .resumeItem {
        margin-bottom: 4%;
    }

    .resumeItem__top > span,
    .resumeItem__bottom > h4,
    .resumeItem__bottom > p {
        font-size: smaller;
    }

    .resumeItem__top > span {
        margin-left: -13px;
    }

    .resumeItem__bottom {
        margin-left: 4%;
        margin-top: 3%;
    }
}

@media screen and (max-width: 950px) {
    .resumeItem__top > span,
    .resumeItem__bottom > h4,
    .resumeItem__bottom > p {
        font-size: small;
    }

    .resumeItem__top > p {
        font-size: 12px;
    }
}